import { Component, OnInit } from '@angular/core';
import { SpotifyService } from '../../services/spotify.service';
import { ArtistData } from '../../data/artist-data';
import { AlbumData } from '../../data/album-data';
import { TrackData } from '../../data/track-data';
import { ResourceData } from '../../data/resource-data';
import { SelectorContext } from '@angular/compiler';
declare var $: any;
@Component({
  selector: 'app-search',
  templateUrl: './search.component.html',
  styleUrls: ['./search.component.css'],
  providers: [ SpotifyService ]
})

export class SearchComponent implements OnInit {
  searchString:string;
  searchCategory:string = 'artist';
  searchCategories:string[] = ['artist', 'album', 'track'];
  resources:ResourceData[];
  carouselId:string;
  hideAlbums

  constructor(private spotifyService:SpotifyService) { }

  ngOnInit() {
    this.carouselId = "carouselMain";
    this.searchCategories.forEach(e => {
      $("#cateList").append(new Option(e, e));  
    });
  }

  search() {
    //TODO: call search function in spotifyService and parse response
    this.searchCategory = $("#cateList :selected").text();
    let searchText:string = this.searchString;

    this.spotifyService.searchFor(this.searchCategory, searchText).then((result) => {
      this.resources = result;
    });
  }
}
