import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { ArtistData } from '../data/artist-data';
import { AlbumData } from '../data/album-data';
import { TrackData } from '../data/track-data';
import { ResourceData } from '../data/resource-data';
import { ProfileData } from '../data/profile-data';
import { TrackFeature } from '../data/track-feature';

@Injectable({
  providedIn: 'root'
})
export class SpotifyService {
	expressBaseUrl:string = 'http://localhost:8888';

  constructor(private http:HttpClient) { }

  private sendRequestToExpress(endpoint:string):Promise<any> {
    //TODO: use the injected http Service to make a get request to the Express endpoint and return the response.
    //the http service works similarly to fetch(). It may be useful to call .toPromise() on any responses.
    //update the return to instead return a Promise with the data from the Express server
    
    return this.http.get(this.expressBaseUrl + endpoint).toPromise();
  }

  aboutMe():Promise<ProfileData> {
    //This line is sending a request to express, which returns a promise with some data. We're then parsing the data 
    return this.sendRequestToExpress('/me').then((data) => {
      return new ProfileData(data);
    });
  }

  searchFor(category:string, resource:string):Promise<ResourceData[]> {
    //TODO: identify the search endpoint in the express webserver (routes/index.js) and send the request to express.
    //Make sure you're encoding the resource with encodeURIComponent().
    //Depending on the category (artist, track, album), return an array of that type of data.
    //JavaScript's "map" function might be useful for this, but there are other ways of building the array.
    let requestStr:string = '/search/' + category + '/' + resource;
    encodeURIComponent(requestStr);
    
    return this.sendRequestToExpress(requestStr).then((data) => {
      switch(category)
      {
        case "artist":
          let artistData:ArtistData[] = [];
          data["artists"]["items"].forEach(e => {
            artistData.push(new ArtistData(e));  
          });
          return artistData;
        case "album":
          let albumData:AlbumData[] = [];
          data["albums"]["items"].forEach(e => {
            albumData.push(new AlbumData(e));  
          });
          return albumData;
        case "track":
          let trackData:TrackData[] = [];
          data["tracks"]["items"].forEach(e => {
            trackData.push(new TrackData(e));  
          });
          return trackData;
      }
    });
  }

  getArtist(artistId:string):Promise<ArtistData> {
    //TODO: use the artist endpoint to make a request to express.
    //Again, you may need to encode the artistId.
    let requestStr:string = '/artist/' + artistId;
    encodeURIComponent(requestStr);

    return this.sendRequestToExpress(requestStr).then((data) => {
      return new ArtistData(data);
    });
  }

  getRelatedArtists(artistId:string):Promise<ArtistData[]> {
    //TODO: use the related artist endpoint to make a request to express and return an array of artist data.
    let requestStr:string = '/artist-related-artists/' + artistId;
    encodeURIComponent(requestStr);

    return this.sendRequestToExpress(requestStr).then((data) => {
      let artistData:ArtistData[] = [];

      data['artists'].forEach(artist => {
        artistData.push(new ArtistData(artist));
      });

      return artistData;
    });
  }

  getTopTracksForArtist(artistId:string):Promise<TrackData[]> {
    //TODO: use the top tracks endpoint to make a request to express.
    let requestStr:string = '/artist-top-tracks/' + artistId;
    encodeURIComponent(requestStr);

    return this.sendRequestToExpress(requestStr).then((data) => {
      let topTrackData:TrackData[] = [];

      data['tracks'].forEach(track => {
        topTrackData.push(new TrackData(track));
      });

      return topTrackData;
    });
  }

  getAlbumsForArtist(artistId:string):Promise<AlbumData[]> {
    //TODO: use the albums for an artist endpoint to make a request to express.
    let requestStr:string = '/artist-albums/' + artistId;
    encodeURIComponent(requestStr);

    return this.sendRequestToExpress(requestStr).then((data) => {
      let albumData:AlbumData[] = [];

      data['items'].forEach(e => {
        albumData.push(new AlbumData(e));
      });

      return albumData;
    });
  }

  getAlbum(albumId:string):Promise<AlbumData> {
    //TODO: use the album endpoint to make a request to express.
    let requestStr:string = '/album/' + albumId;
    encodeURIComponent(requestStr);

    return this.sendRequestToExpress(requestStr).then((data) => {
      return new AlbumData(data);
    });
  }

  getTracksForAlbum(albumId:string):Promise<TrackData[]> {
    //TODO: use the tracks for album endpoint to make a request to express.
    let requestStr:string = '/album-tracks/' + albumId;
    encodeURIComponent(requestStr);

    return this.sendRequestToExpress(requestStr).then((data) => {
      let trackData:TrackData[] = [];

      data['items'].forEach(track => {
        trackData.push(new TrackData(track));
      });

      return trackData;
    });
  }

  getTrack(trackId:string):Promise<TrackData> {
    //TODO: use the track endpoint to make a request to express.
    let requestStr:string = '/track/' + trackId;
    encodeURIComponent(requestStr);

    return this.sendRequestToExpress(requestStr).then((data) => {
      return new TrackData(data);
    });
  }

  getAudioFeaturesForTrack(trackId:string):Promise<TrackFeature[]> {
    //TODO: use the audio features for track endpoint to make a request to express.
    let requestStr:string = '/track-audio-features/' + trackId;
    encodeURIComponent(requestStr);
    let features:TrackFeature[] = [];

    return this.sendRequestToExpress(requestStr).then((data) => {
      TrackFeature.FeatureTypes.forEach(e => {
        features.push(new TrackFeature(e, data[e]));
      });

      return features;
    });
  }
}
